/* MainContent.css */

.PartitionA-SectionC h1 {
  font-weight: 700;
  font-size: 33px;
  border-radius: 15px 15px;  
  width: 227px;
  height: 70px;
  display: flex;
  justify-content: center;  
  align-items: center;  
  border: 2px solid gray;
  background-color: rgb(255, 255, 255);
  color: rgb(34, 34, 34);
  padding: 8px 10px; 
  margin-left: 20px;
}
.PartitionA-SectionB p { 
  font-size: 35px;  
  padding-left:20px;
  padding-right: 10px;
  color: rgb(34, 34, 34);    
  height: 117px;
  margin-top: -20px; 
}
.PartitionA-SectionB h1 {
  font-weight: 700;
  font-size: 33px;
  border-radius: 10px 10px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(255, 255, 255);
  color: rgb(34, 34, 34);
  padding: 8px 10px; 
}

.PartitionA-SectionA p { 
  font-size: 35px;  
  padding-left:20px;
  padding-right: 10px;
  color: rgb(34, 34, 34);    
  height: 117px;
  margin-top: -20px;

}
.PartitionA-SectionA h1 {
  font-weight: 700;
  font-size: 33px;
  border-radius: 10px 10px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(255, 255, 255);
  color: rgb(34, 34, 34);
  padding: 8px 10px; 
}
.PartitionA-SectionB {
  text-align: left;
  display: block;
}
.PartitionA-SectionA {
  text-align: left;
  display: block;
}
.PartitionA {
  float: left;
  display: block;
  width: 545px;
  height: 100%;
  /* background-color: #e8f1dc; */
  background: rgba(232, 241, 220, 0.7);
  border-top-right-radius: 20px;
}

.PartitionB {
  float: right;
  display: block;
  width: 1353px;
  height: 100%;
  /* background-color: #e8f1dc; */
  background: rgba(192, 192, 192, 0.3);
  border-top-left-radius: 20px;
}

.main-content {
  /* background-color: green; *remove later - so */
  animation: fadeIn 1s ease;
  /**Main content 795px + footer 71px;*/
  height: 866px;
}
 
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive styles */
@media only screen and (max-width: 600px) {
  .main-content {
    padding: 2rem;
  }
}

@media only screen and (max-width: 400px) {
  .main-content {
    padding: 1rem;
  }
}
